.search {
  position: relative;
}

input {
    border: 1px solid rgba(0, 0, 0, 0.08);
    padding: 15px;
    border-radius: 15px;
    width: 100%;
    font-size: 16px;
    padding-left: 50px;
}

.search svg {
    width: 18px;
    height: 18px;
    position: absolute;
    top: 17px;
    left: 20px;
    opacity: 0.3;
}

.users-list {
  list-style: none;
  padding: 0;
  margin: 0;
  /* overflow: auto; */
  height: calc(100% - 130px);
  margin-top: 30px;
}

.skeleton-list {
  height: calc(100% - 130px);
  margin-top: 30px;
}

.skeleton-list svg {
  margin-bottom: 15px;
}

.send-invite-btn {
  background-color: rgb(251, 80, 0);
  padding: 16px 15px;
  border-radius: 15px;
  font-size: 16px;
  color: #fff;
  width: 100%;
  border: 0;
  outline: none;
  box-shadow: 0 7px 10px rgba(251, 80, 0, 0.4);
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

.send-invite-btn:hover {
    background-color: rgb(255, 97, 24);
}

.send-invite-btn:active {
    transform: translateY(2px);
    box-shadow: none;
}
